import React, { useEffect, useState, useRef } from 'react';
import logo from './logo.svg';
import './App.css';
import Recipe from './Recipe';

const App = () => {
  const APP_ID = '2c9232ad';
  const APP_KEY = '7620369b9dc2f408349d5afa69b1edd7';

  const [recipes, setRecipes] = useState([]);
  const [search, setSearch] = useState('');
  const searchRef = useRef();
  const [query, setQuery] = useState('chicken');

  useEffect(() => {
    getRecipes();
  }, [query]);

  const getRecipes = async () => {
    const response = await fetch(`https://www.hongkongairport.com/flightinfo-rest/rest/flights/past?date=2021-3-11&lang=en&cargo=false&arrival=true`);
    
    const data = await response.json();
    console.log(data.hits);
    setRecipes(data.hits);
  }

  // const getRecipes = async () => {
  //   const response = await fetch(`https://api.edamam.com/search?q=${query}&app_id=${APP_ID}&app_key=${APP_KEY}`);
    
  //   const data = await response.json();
  //   //console.log(data.hits);
  //   setRecipes(data.hits);
  // }


  /*
  React.useEffect(() => {
    axios
        .get('api/profile/')
        .then(function (response) {
            console.log(response);
        })
        .catch(function (error) {
            console.log(error);
        });
  });
  
  
  
  
  */

  function updateChange(e) {
    const name = searchRef.current.value;
    if (name === '') return;
    setSearch(name);
    //console.log(search);
  }

  function handleSearch(e) {
    e.preventDefault();
    setQuery(search);
    searchRef.current.value = null;

  }

  return (
    <div className="App">
      <form className="search-form" onSubmit={handleSearch}>
        <input className="search-bar" type="text" ref={searchRef} onChange={updateChange} />
        <button className="search-button" type="submit">
          Search
        </button>
      </form>
      <div className="recipes">
        {recipes.map(recipe => (
          <Recipe
            key={recipe.recipe.label}
            title={recipe.recipe.label}
            calories={recipe.recipe.calories.toFixed(0)}
            image={recipe.recipe.image}
            ingredients={recipe.recipe.ingredients}
          />
        ))}
      </div>
    </div>
  );
}

export default App;
